class App extends Component {
  constructor() {
    super();
    this.state = { currentImage: 0,
        charLightboxOpen: false,
        illustLightboxOpen: false,
    };
    this.closeIllustLightbox = this.closeIllustLightbox.bind(this);
    this.openIllustLightbox = this.openIllustLightbox.bind(this);
    this.closeSketchLightbox = this.closeSketchLightbox.bind(this);
    this.openSketchLightbox = this.openSketchLightbox.bind(this);
    this.closeGameLightbox = this.closeGameLightbox.bind(this);
    this.openGameLightbox = this.openGameLightbox.bind(this);
    this.gotoNext = this.gotoNext.bind(this);
    this.gotoPrevious = this.gotoPrevious.bind(this);
  }
  openIllustLightbox(event, obj) {
    this.setState({
      currentImage: obj.index,
      charLightboxOpen: true,
    });
  }
  closeIllustLightbox() {
    this.setState({
      currentImage: 0,
      charLightboxOpen: false,
    });
  }
  openSketchLightbox(event, obj) {
      this.setState({
          currentImage: obj.index,
          illustLightboxOpen: true,
      })
  }
  closeSketchLightbox() {
    this.setState({
      currentImage: 0,
      illustLightboxOpen: false,
    });
  }
  openGameLightbox(event, obj) {
      this.setState({
          currentImage: obj.index,
          comicLightboxOpen: true,
      })
  }
  closeGameLightbox() {
    this.setState({
      currentImage: 0,
      comicLightboxOpen: false,
    });
  }
  gotoPrevious() {
    this.setState({
      currentImage: this.state.currentImage - 1,
    });
  }
  gotoNext() {
    this.setState({
      currentImage: this.state.currentImage + 1,
    });
  }
  render() {
    return (
        <ParallaxProvider>
          <div className="App">
              <ScrollingColorBackground
            selector='.js-color-stop[data-background-color]'
            colorDataAttribute='data-background-color'
            initialRgb='rgb(255, 255, 255)'
          />
          <section
  data-background-color={bg1}
  className='js-color-stop'
>
            <Header/>
            </section>


          <IllustrationGallery openLightbox={this.openIllustLightbox}/>

          <SketchGallery openLightbox={this.openSketchLightbox}/>

          <GameGallery openLightbox={this.openGameLightbox}/>

          <ContactInfo/>


          <Lightbox images={illust_photos}
          onClose={this.closeIllustLightbox}
          currentImage={this.state.currentImage}
          isOpen={this.state.charLightboxOpen}
          onClickPrev={this.gotoPrevious}
          onClickNext={this.gotoNext}
          backdropClosesModal
          showImageCount={false}
          showCloseButton={false}
        />
        <Lightbox images={sketch_photos}
        onClose={this.closeSketchLightbox}
        currentImage={this.state.currentImage}
        isOpen={this.state.illustLightboxOpen}
        onClickPrev={this.gotoPrevious}
        onClickNext={this.gotoNext}
        backdropClosesModal
        showImageCount={false}
        showCloseButton={false}
      />
      <Lightbox images={game_photos}
      onClose={this.closeGameLightbox}
      currentImage={this.state.currentImage}
      isOpen={this.state.comicLightboxOpen}
      onClickPrev={this.gotoPrevious}
      onClickNext={this.gotoNext}
      backdropClosesModal
      showImageCount={false}
      showCloseButton={false}
    />
    <Parallax
offsetYMax={0}
offsetYMin={-20}
  slowerScrollRate
tag="figure"
className="parallax">
    <div className="stars stars-front"/>
  </Parallax>
  <Parallax
offsetYMax={5}
offsetYMin={-5}
slowerScrollRate
tag="figure"
className="parallax">
  <div className="stars stars-back"/>
</Parallax>
          </div>
        </ParallaxProvider>
    );
  }
}

export default App;
